.form-container {
    max-width: 600px; /* iu chnh kch thc ti a */
    margin: 0 auto;
    margin-top: 50px;
    padding: 20px;
    box-sizing: border-box;
    background-color: #ffffff; /* Mu nn nh  phn bit */
    border-radius: 8px; /* Bo gc cho form */
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1); /* Hiu ng  bng nh */
}

.form-title {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
    font-weight: bold;
}

.form-header {
    margin-bottom: 20px;
    font-size: 18px;
    text-align: left;
}

.auction-form {
    display: flex;
    flex-direction: column;
    align-items: stretch;
}

.input-field {
    width: 100%;
    margin-bottom: 15px;
    padding: 10px;
    font-size: 16px;
    box-sizing: border-box;
    border-radius: 4px; /* Bo gc cho cc  input */
    border: 1px solid #ced4da; /* Mu vin */
}

.image-upload {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
}

.image-placeholder {
    width: 32%;
    height: 100px; /* Gim chiu cao cho hnh nh nh hn */
    border: 1px solid #ccc;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background-color: #e9ecef;
    overflow: hidden;
    border-radius: 4px; /* Bo gc cho cc placeholder */
    position: relative; /*  s dng ::before */
}

.image-placeholder::before {
    content: '+';
    font-size: 24px;
    color: #999;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    pointer-events: none; /*  trnh nh hng n s kin nhp chut */
}

.image-placeholder img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

input[type="file"] {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0; /* n input file  ch hin th khung hnh nh */
    cursor: pointer; /* Thay i con tr khi di chut ln khung */
}

.auction-duration-section, .shipping-method-section {
    display: flex;
    flex-direction: column;
}

.auction-duration-label, .shipping-method-label {
    margin-bottom: 5px;
    font-size: 16px;
}

.submit-button {
    background-color: #0b3d91;
    color: white;
    padding: 10px; /* Gim padding cho nt submit */
    font-size: 16px;
    border: none;
    cursor: pointer;
    margin-top: 20px;
    border-radius: 4px; /* Bo gc cho nt */
}

.submit-button:hover {
    background-color: #09418c;
}

